body {
  text-align: center;
  background: #cacac7;
  font-family: sans-serif;
  font-weight: 100;
}

h1 {
  color: #000e5f;
  font-size: 40px;
}

.field {
  font-size: 20px;
  padding: 10px;
  border-radius: 3px;
  background: #cacac7;
  display: inline-block;
}

.value {
  padding: 15px;
  border-radius: 3px;
  background: #e2e2e2;
  display: inline-block;
  font-size: 56px;
}

.label {
  display: block;
  padding-top: 5px;
  font-size: 56px;
}

.datepicker-input{
  height: 100px;
  width: 300px;
  font-size: 38px;
  text-align: center;
}

.controls{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

a{
  font-size: 48px;
}

button{
  height: 70px;
  width: 210px;
  font-size: 30px;
}